.toggle-mnu{
	position: absolute;
	right: rem(30);
	top: rem(5);
  display: block;
  width: rem(28);
  height: rem(28);
  // margin-top: rem(14);
  padding: 0;
  background-color: transparent;
  border: none;
  z-index: 2;

  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: rem(9);
  } 

  span:after{

    top: rem(18);
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: rem(2);
    background-color: #000;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: rem(2);
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(rem(-1), 0);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(rem(6), rem(-7));
  } 
} 
